import React, { createContext, useState, useContext, useEffect } from 'react';

// Create a context
const JobContext = createContext();

// Custom hook to use the context
export const useJobContext = () => {
  return useContext(JobContext);
};

// Context provider component
export const JobProvider = ({ children }) => {
  const [data, setData] = useState(null); // Store the entire JSON data
  const [groupDetails, setGroupDetails] = useState(null); // Store the details for the selected job
  const [groupTitle, setGroupTitle] = useState(null); // Store the title of the selected job
  const [expandData, setExpandData] = useState(null); // Store the expandable data

  // Fetch the JSON files only once when the component mounts
  useEffect(() => {
    fetch("/summaries.json")
      .then((response) => {
        if (!response.ok) {
          throw new Error("Failed to fetch data");
        }
        return response.json();
      })
      .then((jsonData) => {
        setData(jsonData); // Store the fetched data
        // Select a random job when the data is first loaded
        selectRandomJob(jsonData);
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });

    fetch("/job_skills.json")
      .then((response) => {
        if (!response.ok) {
          throw new Error("Failed to fetch data");
        }
        return response.json();
      })
      .then((jsonData) => {
        setExpandData(jsonData); // Store the expandable data
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
  }, []);

  // Function to select a random job from the data
  const selectRandomJob = (jsonData) => {
    const keys = Object.keys(jsonData);
    const randomKey = keys[Math.floor(Math.random() * keys.length)];

    setGroupTitle(randomKey); // Set group title to the randomly selected key
    setGroupDetails(jsonData[randomKey]); // Set group details to the value of the randomly selected key
  };

  const selectSpecificJob = (jsonData, selectedKey) => {
    setGroupTitle(selectedKey); // Set group title to the randomly selected key
    setGroupDetails(jsonData[selectedKey]); // Set group details to the value of the randomly selected key
  };

  return (
    <JobContext.Provider value={{ data, groupDetails, groupTitle, expandData, selectSpecificJob, selectRandomJob, setGroupDetails, setGroupTitle }}>
      {children}
    </JobContext.Provider>
  );
};
