import React from 'react';
import JobDetails from "./components/JobDetails";
import JobExpandableList from "./components/JobExpandableList";
import { Button, Typography, AppBar, Toolbar, Box } from '@mui/material';
import { JobProvider, useJobContext } from './context/JobProvider'; // Import JobProvider

function App() {
  const { data, expandData, selectRandomJob } = useJobContext();

  // Function to handle random selection on button click
  const handleRandomSelection = () => {
    if (data) {
      selectRandomJob(data); // Select a random job from the already loaded data
    }
  };

  return (
    <div>
      {/* Navbar with Shuffle Button */}
      <AppBar position="sticky" sx={{ backgroundColor: '#333' }}>
        <Toolbar>
          <Typography variant="h6" sx={{ flexGrow: 1 }}>
          Hello World!
          </Typography>
          <Button
            variant="contained"
            color="primary"
            onClick={handleRandomSelection}
            sx={{
              marginLeft: 'auto', // Aligns the button to the right
            }}
          >
            Shuffle Job
          </Button>
          {/* New button linking to an external website */}
          <Button
            variant="contained"
            color="secondary"
            onClick={() => window.location.href = 'https://eric-yim.github.io/demos/sts_map'} // External link
            sx={{
              marginLeft: '10px', // Adds space between buttons
            }}
          >
            Map View
          </Button>
        </Toolbar>
      </AppBar>

      {/* Main Content */}
      <Box sx={{ marginTop: '30px', marginBottom: '90px' }}> {/* Margin to avoid content being hidden under the AppBar */}
        {/* Render JobDetails and random selection button if groupDetails and groupTitle are available */}
        
          <>
            <JobDetails />
          </>
        

        {/* Render JobExpandableList if expandData is available */}
        {expandData ? (
          <JobExpandableList />
        ) : (
          <Typography>Loading...</Typography>
        )}
      </Box>
    </div>
  );
}

// Wrap the entire app with the JobProvider
export default function AppWrapper() {
  return (
    <JobProvider>
      <App />
    </JobProvider>
  );
}
