import React, { useState } from "react";
import { Accordion, AccordionSummary, AccordionDetails, Typography, Button, Box } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { useJobContext } from '../context/JobProvider'; // Import the JobContext

const JobExpandableList = () => {
  const [expanded, setExpanded] = useState(false);
  const { expandData, data, selectSpecificJob } = useJobContext(); // Get the context values and setters

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  // Function to handle the job button click
  const handleJobClick = (job) => {

    selectSpecificJob(data, job);
  };

  return (
    <Box>
      {Object.keys(expandData).map((key) => (
        <Accordion
          key={key}
          expanded={expanded === key}
          onChange={handleChange(key)}
          sx={{ marginBottom: 2 }}
        >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls={`${key}-content`}
            id={`${key}-header`}
            sx={{
                backgroundColor: "#f5f5f5", // Light background color
                '&:hover': { backgroundColor: "#e0e0e0" }, // Slightly darker on hover
            }}
          >
            <Typography variant="h6">{key}</Typography>
          </AccordionSummary>
          <AccordionDetails>
            {expandData[key].map((job, index) => (
              <Button
                key={index}
                variant="contained"
                color="primary"
                sx={{ marginBottom: 1, width: "100%" }}
                onClick={() => handleJobClick(job)} // Add onClick handler to set the context
              >
                Jobs like {job}
              </Button>
            ))}
          </AccordionDetails>
        </Accordion>
      ))}
    </Box>
  );
};

export default JobExpandableList;
