import React, { useEffect, useState } from "react";
import {
  Container,
  Typography,
  Card,
  CardContent,
  Grid,
  Link,
  Button,
  CardMedia
} from "@mui/material";
import { useJobContext } from '../context/JobProvider'; // Import the JobContext

const JobDetails = () => {
  const { groupDetails, groupTitle } = useJobContext(); // Consume the values from context
  const [jobTitle, setJobTitle] = useState("");
  const [data, setData] = useState(null);
  const [summaryLines, setSummaryLines] = useState([]);

  // Fetch data when groupDetails or groupTitle changes
  useEffect(() => {
    if (groupDetails) {
      const title = groupTitle;
      setJobTitle(title);
      setData(groupDetails);

      // Safely split summary by \n, if summary exists
      if (groupDetails.summary) {
        setSummaryLines(groupDetails.summary.split('\n').filter(Boolean));
      }
    }
  }, [groupDetails, groupTitle]); // Dependency array ensures this runs whenever groupDetails or groupTitle change

  // Loading state
  if (!data || !summaryLines.length) return <Typography>Loading...</Typography>;

  // Sort interviewees by salary_number in descending order
  const sortedInterviewees = [...data.interviewees].sort(
    (a, b) => b.salary_number - a.salary_number
  );

  const capitalizeWords = (str) => {
    if (typeof str !== 'string') {
      console.error('Input is not a string:', str);
      return ''; // Return an empty string or handle appropriately
    }

    return str
      .split(' ') // Split the string into words
      .map(word => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase()) // Capitalize first letter of each word
      .join(' '); // Join words back together
  };

  return (
    <Container>
      {/* Conditional rendering if groupDetails and groupTitle are available */}
      {groupDetails && groupTitle ? (
        <>
          <Typography variant="h4" gutterBottom >
            Jobs like {capitalizeWords(jobTitle)}
          </Typography>

          {/* Interviewees Section */}
          <Grid container spacing={2}>
            {sortedInterviewees.map((interviewee, index) => (
              <Grid item xs={6} sm={3} md={2} key={index}>
                <Card>
                  <CardContent>
                    {/* Job Title */}
                    <Typography
                      variant="body2"
                      sx={{
                        minHeight: "2.5em",   // Minimum height for 2 lines of text
                        lineHeight: "1.25em",  // Line height to control spacing between lines
                        overflow: "hidden", 
                        fontWeight: "bold"   
                      }}>
                        {capitalizeWords(interviewee.title)}
                    </Typography>
                    <Typography variant="body2">${interviewee.salary_number.toLocaleString()}</Typography>

                    {/* Image as a link */}
                    <Link href={interviewee.link} target="_blank" rel="noopener noreferrer">
                      <CardMedia
                        component="img"
                        width="72"  // Set the width to 72px for the thumbnail
                        height="128" // Set the height to 128px for the thumbnail
                        image={`https://eric-yim.github.io/demos/thumbs/${interviewee.idx}.png`}
                        alt={interviewee.title}
                        sx={{
                          display: 'block',
                          margin: '0 auto',  // Centers the image within the card
                          maxWidth: '72px',   // Ensures the image width stays at 72px
                          maxHeight: '128px', // Ensures the image height stays at 128px
                        }}
                      />
                    </Link>

                    {/* See Interview Button */}
                    <Link href={interviewee.link} target="_blank" rel="noopener noreferrer">
                      <Button variant="contained" color="primary" sx={{ marginTop: 2 }}>
                        See Interview
                      </Button>
                    </Link>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>

          {/* Summary Section */}
          <Typography variant="h5" gutterBottom sx={{ marginTop: 4 }}>
            AI Summary
          </Typography>
          {summaryLines.map((line, index) => (
            <Typography key={index} variant="body1" paragraph sx={{ display: "block" }}>
              {line}
            </Typography>
          ))}
        </>
      ) : (
        <Typography>Loading...</Typography>
      )}
    </Container>
  );
};

export default JobDetails;
